import React, { useState } from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import {ModalRoot, ModalPage, ModalPageHeader, FormLayout, SelectMimicry, FormLayoutGroup, Radio} from '@vkontakte/vkui';

	const directionList = ['Программная инженерия', 'Бизнес-информатика'];
	const coursesList = ['1', '2', '3', '4', 'Магистратура-1', 'Магистратура-2'];
	const groupsList = ['ПрИ-101', 'ПрИ-102', 'ПрИ-201', 'БИ-101'];

	const ModalGroupSelector = ({id, go, open}) => {
	const [modal, setOneModal] = useState(open ? open : 'empty');
	//if(open != 'empty') setOneModal(open);

	return(
		<ModalRoot id={id} activeModal={modal}>
		<ModalPage
			id="groupSelectorPage"
			header={
				<ModalPageHeader>
					Выберите группу
				</ModalPageHeader>
			}
			onClose={
				() => setOneModal('groupSelectorPage')
			}>
			<FormLayout>
				<SelectMimicry top="Направление" placeholder="Выбрать направление" onClick={() => setOneModal('direction')}/>
				<SelectMimicry top="Курс" placeholder="Выбрать курс" onClick={() => setOneModal('course')}/>
				<SelectMimicry top="Группа" placeholder="Выбрать группу" onClick={() => setOneModal('group')}/>
				<Button size="xl" onClick={go} data-to="">Погнали</Button>
			</FormLayout>
		</ModalPage>
		<ModalPage
			id="direction"
			header={
				<ModalPageHeader>
					Выберите направление
				</ModalPageHeader>
		}>
		<FormLayoutGroup>
			{
				directionList.map(
					(title, id) => {
						return (
							<Radio key={id} name="direction" value={id} onClick="">{title}</Radio>
						);
					}
				)
			}
		</FormLayoutGroup>
		<Button size="xl" onClick={() => setOneModal('course')}>Выбрать курс</Button>
		</ModalPage>
		<ModalPage
			id="course"
			header={
				<ModalPageHeader>
					Выберите курс
				</ModalPageHeader>
		}>
		<FormLayoutGroup>
			{
				coursesList.map(
					(title, id) => {
						return (
							<Radio key={id} name="direction" value={id} onClick="">{title}</Radio>
						);
					}
				)
			}
		
		</FormLayoutGroup>
		<Button size="xl" onClick={() => setOneModal('group')}>Выбрать группу</Button>
		</ModalPage>
		<ModalPage
			id="group"
			header={
				<ModalPageHeader>
					Выберите группу
				</ModalPageHeader>
		}>
		<FormLayoutGroup>
			{
				groupsList.map(
					(title, id) => {
						return (
							<Radio key={id} name="direction" value={id} onClick="">{title}</Radio>
						);
					}
				)
			}
		</FormLayoutGroup>
		<Button size="xl" onClick={() => setOneModal('groupSelectorPage')}>Готово</Button>
		</ModalPage>
	</ModalRoot>
);
}
export default ModalGroupSelector;